.onlineCard {
  width: 100%;
  height: 25px;
  display: flex;
  align-items: center;
  padding-left: 15px;
  padding-top: 15px;
  margin-bottom: 5px;
}
.imgContainer {
  position: relative;
  width: 25px;
  height: 25px;
}
img {
  width: 25px;
  height: 25px;
  border-radius: 5px;
}
.isOnline {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 7px;
  width: 7px;

  border-radius: 50%;
}
.h7 {
  padding-left: 5px;
}
.span {
  padding-left: 10px;
  font-size: 10px;
  color: var(--primaryVariant);
}
